import React, {useEffect, useState} from 'react'
import axios from 'axios'

import Cookies from 'js-cookie';

import Title from './Title'
import Menu from './Menu'

import {MainContainer, TitleContainer, ContentContainer} from '../styles/Admin'

import {URL} from '../variables'

function AdminValidator({children, userType, height, onScroll}) {

    const [validated, setValidated] = useState(false)
    const [menuOpened, setMenuOpened] = useState(false)

    const checkToken = async () => {
        const res = await axios({
            url: `${URL}/admin/valid?type=${userType}`,
            method: 'GET'
        })

        return res.data
    }

    const fetchLogout = async () => {
        const res = await axios({
            url: `${URL}/client/logout`,


            method: 'GET'
        })

        return res.data
    }

    const logout = () => {
        fetchLogout().then(() => {

            window.location.replace('/admin/login')
        }).catch(e => {
            window.location.replace('/admin/login')
        })
    }

    useEffect(() => {
        if (Cookies.get('payload')) {
            checkToken().then(() => {
                setValidated(true)
            }).catch((e) => {
                window.location.replace('/admin/login')
            })
        } else {
            window.location.replace('/admin/login')
        }
        document.getElementById('root').style.overflowX = "hidden"

    }, [])

    return (
        <MainContainer height={height} pLeft={menuOpened ? '15vw' : '4vw'} width='96%' onScroll={onScroll}>
            <Menu
                setMenuOpened={setMenuOpened}
                userType={userType}
                logout={logout}
            />
            <ContentContainer width={menuOpened ? '85%' : '100%'}>
                <TitleContainer>
                    <Title bgNavbar={true}/>
                </TitleContainer>
                {validated && children}
            </ContentContainer>
        </MainContainer>
    )
}

export default AdminValidator
