import React, { useState, useEffect } from 'react'
import { Checkbox } from "../../styles/Labs";

function ResultItem({ onChange, idx, handleInputNameCustomParam, handleInputCustomResult, handleInputNameCustomRef, paramsLength,handleCheckbox }) {

    const [result, setResult] = useState('')
    const [ref, setRef] = useState('')
    const [name, setName] = useState('')
    const [checked, setChecked] = useState(false)


    const handleInputName = e => {
        handleInputNameCustomParam(idx, e.target.value)
        setName(e.target.value)
    }

    const handleInputRef = e => {
        setRef(e.target.value)
        handleInputNameCustomRef(idx, e.target.value)
    }

    const handleInputResult = e => {
        const text = e.target.value
        setResult(text)
        handleInputCustomResult(idx, text)
    }

    const chandleCheckbox = () => {
        handleCheckbox(paramsLength ? paramsLength - 1 + idx : idx, !checked)
        setChecked(!checked)
    }

    return (
        <tr>
            <td>
                <input
                    id={`input${idx}-name`}
                    value={name}
                    onChange={handleInputName}
                    placeholder="Nombre"
                    className="input-custom"
                />
            </td>
            <td>
                <input 
                    id={`input${idx}-result-custom`}
                    value={result}
                    onChange={handleInputResult}
                    className="input-custom"

                />
            </td>
            <td>
                <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                    <input
                        id={`input${idx}-refcustom`}
                        value={ref}
                        onChange={handleInputRef}
                        className="input-custom"

                    />
                    <div style={{ padding: '4px', backgroundColor: 'rgb(240, 240, 240)' }}>
                        <Checkbox bgColor={!checked ? 'white': 'rgb(0,47,91)'} onClick={chandleCheckbox} mgRight='0'/>
                    </div>

                </div>

            </td>
        </tr>
    )
}

export default ResultItem
