import React, { useState, useEffect } from 'react'
import axios from 'axios'

import DateItem from './DateItem'
import TokenExpirate from '../TokenExpirate'

import { HistoryContainer, DateItemContainer, DateItemInfo } from '../../styles/User'

import { URL, maxWidthMobile } from '../../variables'

function History() {

    const [dates, setDates] = useState([])
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [render, setRender] = useState(false)

    const getDates = async () => {
        const res = await axios({
            url: `${URL}/cita/history?start=${dates.length}`,
            method: 'GET'
        })

        return res.data
    }

    const getHeight = () => window.screen.width <= maxWidthMobile ? '5vh' : '1.5vw'

    const resize = () => setTimeout(() => setRender(!render), 100);

    useEffect(() => {
        getDates().then(({ dates }) => {
            setDates(dates)
        }).catch(e => {
            console.log(e)
            if(e.reponse.status === 401){ return setTokenExpirate(true) }
        })
        window.addEventListener('resize', resize)
        return () => window.removeEventListener('resize', resize)
    }, [])

    return (
        <HistoryContainer>
            {dates.length === 0 ? (
                <DateItemContainer
                    height={getHeight()}
                    bgColor='rgb(235, 235, 235)'
                >
                    <DateItemInfo width="100%" justifyContent='center' alignItems='center'>
                        <span className="bold">Todavía no hay citas agendadas</span>
                    </DateItemInfo>
                </DateItemContainer>
            ) : (
                dates.map((date, idx) => (
                    <DateItem 
                        key={date.id}
                        date={date}
                        idx={idx}
                        setTokenExpirate={setTokenExpirate}
                    />
                ))
            )}
            <TokenExpirate modal={tokenExpirate}/>
        </HistoryContainer>
    )
}

export default History
