import styled from 'styled-components'

import { deviceMax } from './device'

const darkBlue = "rgb(0,47,91)"
const lightGray = "rgb(235,235,235)"

export const MainContainer = styled.div`
    width: 100%;
    height: ${props => props.height || '100%'};
    overflow-x: hidden;
    overflow-y: ${props => props.overflowHidden ? 'hidden' : 'auto'};  
`

export const ContentContainer = styled.div`
    width: 100%;
    padding-top: ${props => props.pTop || '10vw'};
    display: flex;
    align-items: center;
    flex-direction: column;
`

export const TitleContainer = styled.div`
    width: ${props => props.width || '76vw'}; 
    height: 5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 2vw;
    border-bottom: 0.2vw solid ${darkBlue};
    margin-bottom: 2vw;

    h1{
        font-family: Trajan;
        color: ${darkBlue};
        font-size: 2vw;
        margin: 0;
    }

    @media screen and (${deviceMax.mobileL}) {
        height: fit-content;
        h1{
            font-size: 5vw;
            margin-bottom: 1vh;
        }
    }
`

export const FormGroupContainer = styled.div`
    width: 100vw;
    height: 60vh;

    @media screen and (${deviceMax.mobileL}) {
        height: fit-content;
    }
`
export const FormGroupItemsContainer = styled.div`
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: row;

    @media screen and (${deviceMax.mobileL}) {
        height: fit-content;
    }
`

export const GroupContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: ${ props => props.fDirection || 'row' };
    justify-content: space-between;
    padding:  0 15vw;
    flex-shrink: 0;
    transform: ${props => `translateX(${props.translateX}vw)`};
    transition: transform 500ms ease-in;
    align-items: ${props => props.alignItems || 'flex-start'};

    @media screen and (${deviceMax.mobileL}) {
        flex-direction: column;
        height: fit-content;
        width: 80vw;
        padding: 0 10vw;
    }
`

export const FormContainer = styled.div`
    width: 50vw;
    @media screen and (${deviceMax.mobileL}) {
        width: 100%;
        margin-bottom: 2vh;
    }
`

export const InputContainer = styled.div`
    width: 50vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: ${ props => props.mgBottom || '1vw' };

    .row{
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .select{
        font-family: Monteserrat;
        width: 37vw;
        flex-shrink: 0;
    }

    .css-yk16xz-control{
        border: 0 !important;
        background-color: rgb(235,235,235) !important;
        width: 100%;
    }

    .css-1pahdxg-control{
        border: 0 !important;
        background-color: rgb(235,235,235) !important;
        &:hover{ border-color: rgb(0,47,91) !important; }
    }

    @media screen and (${deviceMax.mobileL}) {
        flex-direction: column;
        width: 100%;
        align-items: flex-start;
        padding: 0 3vw;

        .select{
            width: 74vw;
        }
    }
`

export const InputLabel = styled.label`
    font-family: MontserratMedium;
    color: ${darkBlue};
    font-size: 1.5vw;
    width: ${props => props.width || '15vw'};

    @media screen and (${deviceMax.mobileL}) {
        font-size: 3vw;
    }
`

export const Input = styled.input`
    background-color: ${ props => props.bgColor || lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 0 1vw;
    height: 2.5vw;
    border: 0;
    font-family: Montserrat;
    font-size: 1vw;
    margin-bottom: ${ props => props.mgBottom || '0'};
    flex-shrink: 0;
    &:focus{ outline: 0 }

    @media screen and (${deviceMax.mobileL}) {
        height: 5vh;
        width: 70vw;
        padding: 1vh 2vw;
        font-size: 2.5vw;
    }
`

export const CheckboxInputContainer = styled.div`
    background-color: ${ props => props.bgColor || lightGray};
    width: 36vw;
    border-radius: 0.3rem;
    padding: 0;
    padding-left: 1vw;
    height: 3vw;
    border: 0;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    flex-shrink: 0;

    input{
        background-color: ${lightGray};
        width: 70%;
        height: 90%;
        border: 0;
        font-family: Montserrat;
        font-size: 1vw;
        &:focus{ outline: 0 }
    }

    div.checkbox-container{
        min-width: 35%
    }

    @media screen and (${deviceMax.mobileL}) {
        width: 70vw;
        padding: 1vh 2vw;
        height: 5vh;
        input{
            width: 100%;
            font-size: 2.5vw;
        }
    }
    
`

export const TextArea = styled.textarea`
    background-color: ${lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 1vw;
    height: 5vw;
    border: 0;
    font-family: Montserrat;
    resize: none;
    font-size: 1vw;
    flex-shrink: 0;
    &:focus{ outline: 0 }

    @media screen and (${deviceMax.mobileL}) {
        height: 20vh;
        width: 70vw;
        padding: 1vh 2vw;
        font-size: 2.5vw;
    }
`

export const DateItemContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const DateItemImage = styled.img`
    width: 12vw;
    height: 12vw;
    border-radius: 0.5rem;
    border: solid 1px ${darkBlue};
    margin-bottom: 0.5vw;
    object-fit: cover;

    @media screen and (${deviceMax.mobileL}) {
        width: 40vw;
        height: 40vw;
    }
`

export const DateItemName = styled.h1`
    font-family: MontserratMedium;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;

    @media screen and (${deviceMax.mobileL}) {
        font-size: 4vw
    }
`

export const DateItemText = styled.h2`
    font-family: MontserratLight;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;

    @media screen and (${deviceMax.mobileL}) {
        font-size: 3vw
    }
`

export const BtnsCarrouselContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: ${props => props.justifyContent};
    width: 80vw;
    margin: 0 10vw;
`

export const BtnCarrousel = styled.button`
    background-color: ${darkBlue};
    width: 2vw;
    height: 2vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0;
    border: 0;
    color: white;
    font-size: 0.7vw;
    position: relative;
    bottom: calc(30vh + 1vw);
    z-index: 1;
    &:hover{ cursor: pointer; }

    @media screen and (${deviceMax.mobileL}) {
        top: 47vh;
        width: 6vh;
        height: 6vh;
        font-size: 2vw;
        position: fixed;
        border: 1px solid white;

        &.left{
            left: 2vw
        }

        &.right{
            right: 2vw;
        }
    }
`

export const ItemsContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
`

export const DateFormContainer = styled.div`
    width: 30vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (${deviceMax.mobileL}) {
        width: 100%;
        margin-top: 2vh;
    }
`

export const CalendarContainer = styled.div`
    width: ${props => props.width || '30vw'};
    max-width: ${props => props.width || '30vw'};
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(240,240,240);
    min-height: ${props => props.minHeight || '40vh'};;
    flex-shrink: 0;

    .calendar-doctor{ width: 80vw !important; }
    .tile-calendar-doctor{ 
        height: 8vh !important;
        font-size: 1vw;
    }

    @media screen and (${deviceMax.mobileL}) {
        width: 100%;
        max-width: 100%;
        min-height: 10vh;

        .calendar-doctor{ width: 80vw !important; }
        .tile-calendar-doctor{ 
            height: 8vh !important;
            font-size: 3vw;
        }
    }
`

export const DateMonth = styled.span`
    font-family: Montserrat;
    font-size: 2vw;
    margin: 1vw 0;

    @media screen and (${deviceMax.mobileL}) {
        font-size: 5vw;
    }
`

export const CalendarNavigation = styled.div`
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    min-height: 4.5vw;
`

export const CalendarNavigationBtnContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
`

export const CalendarNavigationBtn = styled.button`
    background-color: transparent;
    border: 0;
    &:hover{ cursor: pointer; }

    @media screen and (${deviceMax.mobileL}) {
        font-size: 3vw;
        margin: 0 2vw;
    }
`

export const CalendarHourContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 1vw;

    @media screen and (${deviceMax.mobileL}) {
        width: 100%;
        margin: 2vh 0;
        justify-content: space-between;
    }
`

export const CalendarHour = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    span{
        font-family: Montserrat;
        font-size: 1.2vw;
    }

    @media screen and (${deviceMax.mobileL}) {
        width: 45%;
        span{
            font-size: 3vw;
            margin-bottom: 1vh;
        }

        .select{
            width: 100%;
            input{
                width: 100%;
                font-size: 3vw;
            }
        }
    }
`

export const TypeDateContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(230,230,230); 
    margin-left: 1vw;
    border-radius: 0.3rem;
    padding: 0.5vw;
    span:first-child{ margin-bottom: 0.5vw; }
    &:hover{
        cursor: pointer;
    }

    @media screen and (${deviceMax.mobileL}) {
        width: 50%;
    }
`

export const TypeDate = styled.span`
    width: 9vw;
    text-align: center;
    font-family: Montserrat;
    padding: 0.2vw 0;
    border-radius: 0.3rem;
    background-color: ${props => props.doctorType ? `${darkBlue}` : 'rgb(240,240,240)'};
    color: ${props => props.doctorType ? `white` : 'black'};
    font-size: 1vw;

    @media screen and (${deviceMax.mobileL}) {
        width: 35vw;
        height: 4vh;
        font-size: 2.5vw;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 1vh 0;
    }
`

export const IconLabContainer = styled.div`
    width: 12vw;
    border-radius: 0.5rem;
    border: solid 1px ${darkBlue};
    margin-bottom: 0.5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    padding: 1vw 0;

    @media screen and (${deviceMax.mobileL}) {
        padding: 2vw 2vw;
        min-width: 40vw;
        min-height: 40vw;

        svg{
            height: 20vw;
            width: 20vw;
            flex-shrink: 1;
        }
    }
`

export const BtnFinishDate = styled.button` 
    padding: 0.5vw 1vw;
    border-radius: 0.5rem;
    background-color: ${darkBlue};
    font-family: MontserratMedium;
    border: 0;
    font-size: 1vw;
    color: white;
    margin-bottom: 1vw;
    &:hover{ cursor: pointer; }

    @media screen and (${deviceMax.mobileL}) {
        width: ${props => props.width || '76vw'};
        padding: 1vh 2vw;
        font-size: 2.5vw;
        margin-top: 1vh;
        margin-bottom: 2vh;
    }
`

export const ErrMessage = styled.span`
    color: ${props => props.color};
    font-family: MontserratMedium;
    font-size: 1vw;
    text-align: center;
    margin-top: ${props => props.mTop || '1vw'};
    width: ${props => props.width || '12vw'};

    @media screen and (${deviceMax.mobileL}) {
        font-size: 3vw;
    }
`