import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { useHistory } from "react-router-dom";

import MainContainer from '../components/MainContainer'
import DoctorsGroup from '../components/doctors/DoctorsGroup'

import { ErrMessage } from '../styles/Date'

import { URL } from '../variables'

function Doctors() {

    const history = useHistory()

    const [doctors, setDoctors] = useState([])
    const [nutriologist, setNutriologist] = useState([])
    const [chemicals, setChemicals] = useState([])
    const [psychologist, setPsychologist] = useState([])
    const [err, setErr] = useState(false)
    const [message, setMessage] = useState('')

    const getDoctors = async () => {
        let res = await axios({
            method: 'GET',
            url: `${URL}/all-medicos`,
            timeout: 3000
        })
        return res.data
    }

    const redirectDoctor = doctor => history.push(`/medicos/${doctor.id}`)

    useEffect(() => {
        document.getElementById('root').style.overflow = "auto"
        getDoctors().then( ({doctors}) => {
            let docs = doctors.filter( doc => doc.tipo === "MEDICO" )
            let nutri = doctors.filter( doc => doc.tipo === "NUTRIOLOGO" )
            let chemi = doctors.filter( doc => doc.tipo === "QUIMICO" )
            let psy = doctors.filter( doc => doc.tipo === "PSICOLOGO" )
            setDoctors(docs)
            setNutriologist(nutri)
            setChemicals(chemi)
            setPsychologist(psy)
        }).catch( (e) => {
            setErr(true)
            setMessage('Ha oucrrido un error intentando obtener la lista de doctores. Itentanlo más tarde.')
        })
    }, [])

    return (
        <MainContainer>
            <DoctorsGroup 
                doctors={doctors}
                title="MÉDICOS"
                itemWidth={30}
                handleItem={redirectDoctor}
                userType="MEDICO"
            />
            <DoctorsGroup 
                doctors={nutriologist}
                title="NUTRIÓLOGOS"
                closed={true}
                itemWidth={30}
                handleItem={redirectDoctor}
                userType="MEDICO"
            />
            <DoctorsGroup 
                doctors={chemicals}
                title="QUIMICOS"
                closed={true}
                itemWidth={30}
                handleItem={redirectDoctor}
                userType="QUIMICO"
            />
            <DoctorsGroup
                doctors={psychologist}
                title="PSICÓLOGOS"
                closed={true}
                itemWidth={30}
                handleItem={redirectDoctor}
                userType="PSICOLOGO"
            />
            {err && <ErrMessage color="red" width="50vw">{message}</ErrMessage>}
        </MainContainer>
    )
}

export default Doctors
