import React, { useState, useEffect } from 'react'
import Cookies from 'js-cookie';
import axios from 'axios'

import Form from './Form'
import BtnDate from './BtnDate'
import Stars from '../Stars'

import { GroupContainer, DateItemContainer, DateItemImage, DateItemName, DateItemText, IconLabContainer } from '../../styles/Date'

import { ReactComponent as HemIcon } from '../../assets/icons/hematologia.svg';
import { ReactComponent as CoaIcon } from '../../assets/icons/coagulacion.svg';
import { ReactComponent as QuSaIcon } from '../../assets/icons/quimica-sanguinea.svg';
import { ReactComponent as SerIcon } from '../../assets/icons/serologia.svg';
import { ReactComponent as OriIcon, ReactComponent as OrIcon } from '../../assets/icons/orinas.svg';
import { ReactComponent as BacIcon, ReactComponent as BaIcon } from '../../assets/icons/bacterologia.svg';
import { ReactComponent as DroIcon } from '../../assets/icons/drogas.svg';
import { ReactComponent as EspIcon } from '../../assets/icons/especiales.svg';
import { ReactComponent as PHFIcon, ReactComponent as PFemIcon } from '../../assets/icons/p_fem.svg';
import { ReactComponent as PClIcon, ReactComponent as PCliIcon } from '../../assets/icons/p_cli.svg';
import { ReactComponent as PPrIcon, ReactComponent as PProIcon } from '../../assets/icons/p_pro.svg';
import { ReactComponent as EleIcon } from '../../assets/icons/electrolitos.svg'; 

import { URL } from '../../variables'

function UserForm({ dateItem, translateX, dateType, labsDate }) { 

    const [name, setName] = useState('')
    const [age, setAge] = useState('')
    const [illness, setIllness] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [address, setAddress] = useState('')
    const [clientID, setClientID] = useState('')
    const [sex, setSex] = useState('')
    const [homeService, setHomeService] = useState(false)

    const getIconLab = (laboratorio, className) => {
        switch(laboratorio){
            case "Hematologia": return( <HemIcon className="icon-lab"/> )
            case "Coagulacion": return( <CoaIcon className="icon-lab"/> )
            case "Quimica sanguinea": return( <QuSaIcon className="icon-lab"/> )
            case "Serologia": return( <SerIcon className="icon-lab"/> )
            case "Heces Fecales": return( <OriIcon className="icon-lab"/> )
            case "Especiales": return( <EspIcon className="icon-lab"/> )
            case "Orinas": return( <OriIcon className="icon-lab"/> )
            case "Bacteriologia": return( <BacIcon className="icon-lab"/> )
            case "Drogas": return( <DroIcon className="icon-lab"/> )
            case "Electrolitos": return( <EleIcon className="icon-lab"/> )
            case "P. Hormonal Fem": return( <PHFIcon className="icon-lab"/> )
            case "Perfil Climaterio": return( <PClIcon className="icon-lab"/> )
            case "Perfil Prostatico": return( <PPrIcon className="icon-lab"/> )
            default:
                break;
        }
    }

    const getImage = () => {
        console.log(dateItem)
        console.log(dateType)
        if(dateType === "laboratorio"){
            if(dateItem.laboratorio){ return getIconLab(dateItem.laboratorio) }
            else{
                let labsType = getLabsType(labsDate)
                console.log(labsType)
                if(labsType.length === 1){
                    return (
                        <IconLabContainer>
                            {getIconLab(labsType[0].nombre)}
                        </IconLabContainer>
                    )
                }
                else if(labsType.length === 2){
                    return (
                        <IconLabContainer>
                            {labsType.map( lab => getIconLab(lab.nombre, 'icon-medium-lab'))}
                        </IconLabContainer>
                    )
                }
                else{
                    let labs = labsType
                    if(labs.length > 3){ labs.splice(2, labsType.length - 1) }
                    return (
                        <IconLabContainer>
                            {labs.map( lab => getIconLab(lab.nombre, 'icon-medium-lab'))}
                        </IconLabContainer>
                    )
                }
            }
            
        }
        return (<DateItemImage src={dateItem.imagen}/>)
    }

    const getLabsType = labs => {
        let labsType = []
        for(let i = 0; i < labs.length; i++){
            if(!labsType.some( labType => labType.id == labs[i].laboratorio.id )){
                labsType.push(labs[i].laboratorio)
            }
        }
        return labsType
    }

    const getDateDescription = () => {
        if(dateType === "laboratorio"){
            if(dateItem.laboratorio){ 
                return (
                    <>
                    <DateItemName>{dateItem.laboratorio}</DateItemName>
                    <DateItemText>{dateItem.nombre}</DateItemText>
                    </>
                ) 
            }else{
                let labsType = getLabsType(labsDate)
                if(labsType.length === 1){ 
                    return (
                        <>
                        <DateItemName>{labsType[0].nombre}</DateItemName>
                        {labsDate.map( lab =>  <DateItemText key={lab.id}>{lab?.nombre}</DateItemText>)}
                        </>
                    )
                }else{
                    return (
                        labsType.map( labType => (
                            <React.Fragment key={labType.id}>
                                <DateItemName>{labType.nombre}</DateItemName>
                                {labsDate.filter( lab => lab.tipo === labType.id ).map( lab =>  <DateItemText key={lab.id}>{lab?.nombre}</DateItemText>)}
                            </React.Fragment>
                        ) )
                    )
                }
            }
            
        }
        return (
            <>
            <DateItemName>{dateItem.nombre}</DateItemName>
            <DateItemText>{dateItem.cargo}</DateItemText>
            </>
        )
    }

    const getUser = async () => {
        const res = await axios({
            url: `${URL}/client`,
            method: 'GET'
        })

        return res.data
    }

    const setUserInfo = user => {
        setName(user.nombre)
        setAddress(user.direccion)
        setAge(user.edad)
        setPhone(user.telefono)
        setEmail(user.email)
        setClientID(user.id)
        if(user.sexo === "H"){ setSex({ value: 'H', label: 'Hombre' }) }
        else{ setSex({ value: 'M', label: 'Mujer' }) }
    }

    useEffect(() => {
        if(Cookies.get('payload')){
            getUser().then(({user}) => {
                setUserInfo(user)
            }).catch((e) => {
                console.log(e)
            })
        }
    }, [])

    return (
        <GroupContainer 
            translateX={translateX} alignItems={labsDate.length < 10 ? 'center' : null}
            className="user-form"
        >
            <Form 
                name={name}
                age={age}
                illness={illness}
                phone={phone}
                email={email}
                address={address}
                homeService={homeService}
                sex={sex}
                setName={setName}
                setAge={setAge}
                setIllness={setIllness}
                setPhone={setPhone}
                setEmail={setEmail}
                setAddress={setAddress}
                setHomeService={setHomeService}
                setSex={setSex}
                dateType={dateType}
            />
            <DateItemContainer>
                {getImage()}
                <Stars value={dateItem.estrellas || getLabsType(labsDate)[0]?.estrellas} mgBottom="0.4vw"/>
                {getDateDescription()}
                <BtnDate 
                    name={name}
                    age={age}
                    illness={illness}
                    phone={phone}
                    email={email}
                    address={address}
                    homeService={homeService}
                    clientID={clientID}
                    sex={sex}
                    dateItem={dateItem}
                />
            </DateItemContainer>
        </GroupContainer>
    )
}

export default UserForm
