import React, { useEffect, useState } from 'react'

import PromInfo from './PromInfo'
import CalendarComponent from './CalendarComponent'

import { GroupContainer, DateFormContainer } from '../../styles/Date'

import { maxWidthMobile } from '../../variables'

function Calendar({ translateX, dateType, setDateType }) { 

    const [render, setRender] = useState(false)

    const getSizeBold = () => window.screen.width <= maxWidthMobile ? 6 : 3
    const getSize = () => window.screen.width <= maxWidthMobile ? '3vw' : '1.5vw'

    const reRender = () => setTimeout(() => setRender(!render), 100);
    

    useEffect(() => {
        window.addEventListener('resize', reRender)
        return () => window.removeEventListener('resize', reRender)
    }, [])

    return (
        <GroupContainer translateX={translateX}>
            <PromInfo 
                width="30vw" 
                sizeTBold={getSizeBold()}
                sizeTNormal={getSize()}
                lineWidth="60%"
            />
            <DateFormContainer>
                <CalendarComponent 
                    dateType={dateType}
                    setDateType={setDateType}
                    calendarClassName="calendar-doctor"
                    tileClassName="tile-calendar-doctor"
                />
            </DateFormContainer>
        </GroupContainer>
    )
}

export default Calendar
