import React, { useState } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import { FormChangePassword } from '../../styles/ForgotPassword'

import { URL } from '../../variables'

function Form({ token, admin, email, doctor, userType, name }) {

    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState('')
    const [err, setErr] = useState(false)
    const [loading, setLoading] = useState(false)

    const handleInputPassword = e => setPassword(e.target.value)
    const handleInputConfirmPassword = e => setConfirmPassword(e.target.value)

    const changePassword = async () => {
        const res = await axios({
            url: `${URL}/${admin ? 'admin' : 'client'}/password`,
            method: 'PUT',
            data: { password },
            headers:{
                'Authorization': `Barer ${token}`
            }
        })

        return res.data
    }

    const handleBtn = (e) => {
        e.preventDefault()
        const { ok, message } = checkData()
        if(ok && !loading){
            setLoading(true)
            changePassword().then(() => {
                setLoading(false)
                if(admin){ return window.location.replace('/admin/login') }
                window.location.replace('/')
            })
            .catch((e) => {
                console.log(e)
                setLoading(false)
                setErr(true)
                setMessage('HA SUCEDIDO UN ERROR EN EL SERVIDOR. INTENTALO MÁS TARDE')
            })
        }
        setErr(true)
        setMessage(message)
    }

    const checkData = () => {
        if(password !== confirmPassword){ return { ok: false, message: 'LAS CONTRASEÑAS DEBEN COINCIDIR' } }
        if(password.length < 6 || confirmPassword.length < 6){ return { ok: false, message: 'LA CONTRASEÑA DEBE TENER AL MENOS SEIS CARACTERES' } }
        return { ok: true }
    }

    return (
        <FormChangePassword>
            <div>
                <span>Email: </span>
                <span>{email}</span>
            </div>
            {doctor !== "" && doctor && (
                <div>
                    <span>{userType !== "ASISTENTE" ? 'Especialidad:' : 'Cargo:'} </span>
                    <span>{doctor}</span>
                </div>
            )}
            {userType === "CLIENTE" && (
                <div>
                    <span>Nombre:</span>
                    <span>{name}</span>
                </div>
            )}
            <input 
                value={password}
                onChange={handleInputPassword}
                type="password"
                placeholder="NUEVA CONTRASEÑA"
            />
            <input 
                value={confirmPassword}
                onChange={handleInputConfirmPassword}
                type="password"
                placeholder="CONFIRMAR NUEVA CONTRASEÑA"
            />
            <button onClick={handleBtn}>GUARDAR CONTRASEÑA</button>
            {err && <span>{message}</span>}
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
        </FormChangePassword>
    )
}

export default Form
