import React, { useState, useEffect } from 'react'
import Loader from 'react-loader-spinner'
import axios from 'axios'

import { DateItemContainer, DateItemInfo, LoaderContainer } from '../../styles/User'

import { URL, maxWidthMobile } from '../../variables'

function DateItem({ date, idx, setTokenExpirate }) {

    const [open, setOpen] = useState(false)
    const [loading, setLoading] = useState(false)
    const [render, setRender] = useState(false)
    const [recipes, setRecipes] = useState([])

    const twoDigits = number => number < 10 ? `0${number}` : number

    const getHour = () => `${twoDigits(new Date(date.fecha).getHours())}:${twoDigits(new Date(date.fecha).getMinutes())}`

    const getDate = () => {
        const nDate = new Date(date.fecha)
        return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()} ${getHour()}`
    }

    const handleClick = () => {
        setOpen(!open)
        if(!open){
            setLoading(true)
            getRecipe().then(({ recipe }) => {
                setLoading(false)
                setRecipes(recipe.map(recipeDB => recipeDB.receta))
            }).catch(e => {
                console.log(e)
                setLoading(false)
                if(e.response.status === 401){ return setTokenExpirate(true) }
            })
        }
    }

    const getService = () => {
        if(date.tipo === "MEDICO"){ return `${date.nombre} ${date.cargo}` }
        else{ return `${date.servnombre}` }
    }

    const getRecipe = async () => {
        const res = await axios({
            url: `${URL}/cita/${date.id}`,
            method: 'GET'
        })

        return res.data
    }

    const getHeight = () => {
        if(date.tipo === "MEDICO"){
            if(open){
                if(loading){ return window.screen.width <= maxWidthMobile ? `${(Math.ceil(date.padecimiento.length / 75) * 1.1) + 6}vh` : `${(Math.ceil(date.padecimiento.length / 75) * 1.1) + 6}vw` }
                else{
                    return window.screen.width <= maxWidthMobile ? `${(Math.ceil(date.padecimiento.length / 20) * 2.6) + 8 + recipes.length * 2.6}vh` 
                    : `${(Math.ceil(date.padecimiento.length / 75) * 1.1) + 5 + recipes.length * 1.1}vw` 
                } 
            }
            else{ return window.screen.width <= maxWidthMobile ? '4vh' : '1vw' }
        }
        else{
            if(open){ return window.screen.width <= maxWidthMobile ? '7vh' : '1vw' }
            else{ return window.screen.width <= maxWidthMobile ? '4vh' : '1vw' }
        }
    }

    const resize = () => setTimeout(() => setRender(!render), 100);

    useEffect(() => {
        window.addEventListener('resize', resize)
        return () => window.removeEventListener('resize', resize)
    }, [])
    

    return (
        <DateItemContainer
            height={getHeight()}
            bgColor={idx %2 === 0 ? 'rgb(245, 245, 245)' : 'rgb(235, 235, 235)'}
            onClick={handleClick}
        >
            <div className="column">
                <DateItemInfo width="100%">
                    <DateItemInfo width={window.screen.width <= maxWidthMobile ? '40%' : '20%'}>
                        <span className="bold">Fecha:&nbsp;</span>
                        <span> {getDate()}</span>
                    </DateItemInfo>
                    <DateItemInfo width={date.medico ? window.screen.width <= maxWidthMobile ? '60%' : '48%' : window.screen.width <= maxWidthMobile ? '60%' : '33%'}>
                        <span className="bold">{date.tipo === "MEDICO" ? 'Doctor' : 'Laboratorio'}:&nbsp;</span>
                        <span>{getService()}</span>
                    </DateItemInfo>
                    <DateItemInfo width={window.screen.width <= maxWidthMobile ? '40%' : '15%'}>
                            <span className="bold">Pagado:&nbsp;</span>
                            <span>{date.pagado ? 'Si' : 'No'}</span>
                    </DateItemInfo>
                    <DateItemInfo width={window.screen.width <= maxWidthMobile ? '60%' : '17%'}>
                        <span className="bold">Estado:&nbsp;</span>
                        <span>{date.aprobado ? 'Aprobado' : 'Sin aprobar'}</span>
                    </DateItemInfo>
                </DateItemInfo>
                <DateItemInfo width="100%" mTop="1vw">
                    <DateItemInfo width={window.screen.width <= maxWidthMobile ? '40%' : '15%'}>
                        <span className="bold">Domicilio:&nbsp;</span>
                        <span>{date.direccion ? 'Si' : 'No'}</span>
                    </DateItemInfo>
                    {date.tipo === "MEDICO" && (
                        <DateItemInfo width={window.screen.width <= maxWidthMobile ? '60%' : '85%'}>
                            <span className="bold">Padecimiento:&nbsp;</span>
                            <span>{date.padecimiento}</span>
                        </DateItemInfo>
                    )}
                    {date.tipo === "QUIMICO" && (
                        <DateItemInfo width={window.screen.width <= maxWidthMobile ? '60%' : '85%'}>
                            <span className="bold">Quimico:&nbsp;</span>
                            <span>{date.nombre}</span>
                        </DateItemInfo>
                    )}
                </DateItemInfo>
                {date.tipo === "MEDICO" && (
                    <>
                    <DateItemInfo width="100%" mTop="1vw">
                        <span className="bold">Receta:&nbsp;</span>
                        <span>{recipes.length === 0 && 'Todavía no hay una receta'}</span>
                    </DateItemInfo>
                    {recipes.map((recipe, idx) => (
                        <DateItemInfo width="100%" key={idx}>
                            <span className="bold">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                            <span>{recipe}</span>
                        </DateItemInfo>
                    ))}
                    </>
                )}
                {loading && (
                    <LoaderContainer>
                        <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>
                    </LoaderContainer>
                )}
            </div>
        </DateItemContainer>
    )
}

export default DateItem
