import React, { useEffect, useState, useRef } from 'react'
import axios from 'axios'
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import MainContainer from '../components/MainContainer'

import { Image } from '../styles/Services'
import { AdminEditBtnContainer, MessageError } from '../styles/Admin'
import { CarouselBtn } from '../styles/Home'

import { URL as URL_SERVER } from '../variables'

function Services({ adminEdit }) {

    const refInputFile = useRef(null)

    const [img, setImg] = useState('')
    const [message, setMessage] = useState('')
    const [image, setImage] = useState(null)
    const [loading, setLoading] = useState(false)
    const [err, setErr] = useState(false)

    const getImage = async () => {
        const res = await axios({
            url: `${URL_SERVER}/info?services=true`,
            method: 'GET'
        })

        return res.data
    }

    const postImage = async (data) => {
        const res = await axios({
            url: `${URL_SERVER}/admin/imgs`,
            method: 'PUT',
            data
        })

        return res.data
    }

    const handleUploadImage = () => refInputFile.current.click()

    const getFormData = (object) => {
        let formData = new FormData();
        if(image){ formData.append('images', image) }
        Object.keys(object).forEach(key => formData.append(key, object[key]));
        return formData;
    }

    const handleInputImage = e => {
        if(!loading){
            setLoading(true)
            setErr(false)
            if(image){ URL.revokeObjectURL(img) }
            setImage(e.target.files[0])
            setImg(URL.createObjectURL(e.target.files[0]))
        }
    }

    useEffect(() => {
        if(image){
            const data = getFormData({ updateType: 'SERVICIO' })
            postImage(data).then(() => {
                setLoading(false)
            }).catch(e => {
                console.log(e)
                setErr(true)
                setMessage('Ha ocurrido un error intentando actualizar la imagén. Intentalo más tarde o reporta el problema')
            })
        }
    }, [image])

    useEffect(() => {
        getImage().then(({ image }) => {
            setImg(image.url)
        }).catch(e => {
            console.log(e)
        })
    }, [])

    return (
        <MainContainer overflowHidden={true} adminEdit={adminEdit}>
            <Image src={img}/>
            <input 
                style={{ display: 'none' }}
                type="file"
                ref={refInputFile}
                accept="image/*"
                name="image"
                onChange={handleInputImage}
            />
            {adminEdit && (
                <AdminEditBtnContainer>
                    <CarouselBtn onClick={handleUploadImage}>
                        <FontAwesomeIcon icon={faEdit} color="rgb(178,223,252)" size="2x"/>
                    </CarouselBtn>
                </AdminEditBtnContainer>
            )}
            {err && <MessageError>{message}</MessageError>}
        </MainContainer>
    )
}

export default Services
